'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import toast from 'react-hot-toast'
import FormInput from '../inputs/FormInput'
import AuthCard from '../layout/AuthCard'
import { validatePassword } from '@/utils/validators'
import { registerUser, loginUser } from '@/utils/api'

interface AuthFormProps {
  mode: 'login' | 'register'
}

export default function AuthForm({ mode }: AuthFormProps) {
  const router = useRouter()

  const [formData, setFormData] = useState({
    first_name: '',
    surname: '',
    other_names: '',
    email: '',
    pen_number: '',
    password: '',
    confirm_password: '',
  })

  const [errors, setErrors] = useState<Record<string, string>>({})

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData({ ...formData, [name]: value })
    setErrors((prev) => ({ ...prev, [name]: '' }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    const newErrors: Record<string, string> = {}

    if (!formData.pen_number) newErrors.pen_number = 'PEN number is required'
    if (!formData.password) newErrors.password = 'Password is required'

    if (mode === 'register') {
      if (!formData.first_name) newErrors.first_name = 'First name is required'
      if (!formData.surname) newErrors.surname = 'Surname is required'
      if (!formData.email) newErrors.email = 'Email is required'
      if (!validatePassword(formData.password)) {
        newErrors.password = 'Password must include upper, lower, number & symbol'
      }
      if (formData.password !== formData.confirm_password) {
        newErrors.confirm_password = 'Passwords do not match'
      }
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors)
      return
    }

    try {
      if (mode === 'login') {
        await loginUser({ pen_number: formData.pen_number, password: formData.password })
        toast.success('Login successful')
        router.push('/')
      } else {
        await registerUser(formData)
        toast.success('Registration successful')
        router.push('/login')
      }
    } catch (err: any) {
      toast.error(err.message || 'Authentication failed')
    }
  }

  return (
    <AuthCard title={mode === 'login' ? 'Login' : 'Register'}>
      <form onSubmit={handleSubmit} className="space-y-4">
        {mode === 'register' && (
          <>
            <FormInput label="First Name" name="first_name" value={formData.first_name} onChange={handleChange} error={errors.first_name} />
            <FormInput label="Surname" name="surname" value={formData.surname} onChange={handleChange} error={errors.surname} />
            <FormInput label="Other Names (Optional)" name="other_names" value={formData.other_names} onChange={handleChange} />
            <FormInput label="Email" name="email" type="email" value={formData.email} onChange={handleChange} error={errors.email} />
          </>
        )}
        <FormInput label="PEN Number" name="pen_number" value={formData.pen_number} onChange={handleChange} error={errors.pen_number} />
        <FormInput label="Password" name="password" type="password" value={formData.password} onChange={handleChange} error={errors.password} />
        {mode === 'register' && (
          <FormInput label="Confirm Password" name="confirm_password" type="password" value={formData.confirm_password} onChange={handleChange} error={errors.confirm_password} />
        )}

        <button type="submit" className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition">
          {mode === 'login' ? 'Login' : 'Register'}
        </button>
      </form>

      <p className="text-sm text-center mt-4 text-gray-600">
        {mode === 'login' ? (
          <>Don&apos;t have an account? <a href="/register" className="text-blue-600 hover:underline">Register</a></>
        ) : (
          <>Already have an account? <a href="/login" className="text-blue-600 hover:underline">Login</a></>
        )}
      </p>
    </AuthCard>
  )
}
