export function loginValidator(data: { pen_number: string; password: string }) {
  const errors: Record<string, string> = {}

  if (!data.pen_number) errors.pen_number = 'PEN number is required'
  if (!data.password) errors.password = 'Password is required'

  return errors
}

export function registerValidator(data: {
  first_name: string
  surname: string
  email: string
  pen_number: string
  password: string
  confirm_password: string
}) {
  const errors: Record<string, string> = {}

  if (!data.first_name) errors.first_name = 'First name is required'
  if (!data.surname) errors.surname = 'Surname is required'
  if (!data.email) errors.email = 'Email is required'
  if (!data.pen_number) errors.pen_number = 'PEN number is required'

  if (!data.password) {
    errors.password = 'Password is required'
  } else if (!validatePassword(data.password)) {
    errors.password = 'Password must include uppercase, lowercase, number, and symbol'
  }

  if (data.password !== data.confirm_password) {
    errors.confirm_password = 'Passwords do not match'
  }

  return errors
}
